import React from 'react';
import Login from './login';
import Purchase from './purchase';

class MainPurchase extends React.Component {

    render() {
        return (
            <React.Fragment>
                {
                    this.props.isAuthenticated ?
                    <Purchase />
                    :
                    <Login />
                }
            </React.Fragment>
        )
    }
}

export default MainPurchase;