import React from 'react';
import Login from './login';
import Buypackages from './buypackages';

class MainStock3 extends React.Component {

    render() {
        return (
            <React.Fragment>
                {
                    this.props.isAuthenticated ?
                    <Buypackages />
                    :
                    <Login />
                }
            </React.Fragment>
        )
    }
}

export default MainStock3;