import React from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import { Popover, Menu, Dropdown } from 'antd';
import HomeIcon from '@material-ui/icons/Home';
import { UserOutlined, MoneyCollectOutlined, GiftOutlined, FieldTimeOutlined } from '@ant-design/icons';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';

const useStyles = makeStyles((theme) => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: '60vw',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
    },
    toolbarLink: {
        padding: theme.spacing(1),
        flexShrink: 0,
    },
}));


export default function Headered(props) {
    const classes = useStyles();
    const { userinfo, goto, gotoname, title } = props;
    return (
        <>
            <Popover content="Home"> 
                <IconButton>
                    <Link to="/">
                        <HomeIcon />
                    </Link>
                </IconButton>
            </Popover>
            <Popover content={gotoname}> 
                <IconButton>
                    <Link to={goto}>
                        <ShoppingCartIcon />
                    </Link>
                </IconButton>
            </Popover>
            <Popover content="Purchase"> 
                <IconButton>
                    <Link to="/purchase">
                        <MonetizationOnIcon />
                    </Link>
                </IconButton>
            </Popover>
            <Typography component="h2" variant="h5"
                        color="inherit" align="center"
                        noWrap className={classes.toolbarTitle}>
                {title}
            </Typography>
            <Dropdown.Button overlay={(
                <Menu>
                    <Menu.Item key="1" icon={<MoneyCollectOutlined />}>
                        Money: {userinfo.money} (VNĐ)
                    </Menu.Item>
                    <Menu.Item key="2" icon={<GiftOutlined />}>
                        Package: {userinfo.packagename}
                    </Menu.Item>
                    <Menu.Item key="3" icon={<FieldTimeOutlined />}>
                        Expire: {
                            userinfo.packagetime === 0 ?
                            "Vô thời hạn"
                            :
                            new Date(userinfo.packagetime*1000).toUTCString().slice(4,16)
                        }
                    </Menu.Item>
                </Menu>
            )} placement="bottomCenter" icon={<UserOutlined />} trigger="click">
                {userinfo.username}
            </Dropdown.Button>
            <Popover content="Logout">
                <IconButton onClick={() => this.props.logout()}>
                    <ExitToAppIcon />
                </IconButton>
            </Popover>
        </>
    )
}

Headered.propTypes = {
    listlink: PropTypes.object,
};