import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import { Spin, Modal, Popconfirm } from 'antd';
import Toolbar from '@material-ui/core/Toolbar';
import axios from 'axios';
import * as api from '../../api';
import Headered from './headered';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import ShoppingBasketOutlinedIcon from '@material-ui/icons/ShoppingBasketOutlined';
import IconButton from '@material-ui/core/IconButton';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: '60vw',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
    },
    toolbarLink: {
        padding: theme.spacing(1),
        flexShrink: 0,
    },
});

class Buypackages extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            getting: false,
            link: "",
            user: {},
            package: []
        }
    }

    actionBuy(name) {
        this.setState({user:{}});
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_packages_buy,{
            "pakagetype" : name
        })
        .then(resp => {
            this.getuserprofile();
            Modal.success({
                title: 'Buy package success',
                content: `Buy package ${name} success !`,
            });
        })
        .catch(err => {
            this.getuserprofile();
            Modal.error({
                title: 'Buy package error',
                content: err.response.data.msg,
            });
        })
    }

    getuserprofile() {
        this.setState({user:{}});
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_user_detail,{})
        .then(resp => {
            this.setState({user:resp.data})
        })
        .catch(err => {
            console.log(err)
        })
    }

    componentDidMount() {
        this.getuserprofile();
        axios.get(api.api_packages_list)
        .then(resp => {
            this.setState({package: resp.data})
        })
    }

    render() {
        const { classes } = this.props;
        const self = this;
        return (
            <Spin spinning={!this.state.user.username}>
            <Container component="main" maxWidth="lg" style={{overflow: "hidden"}}>
                <React.Fragment>
                    <Toolbar className={classes.toolbar}>
                        <Headered userinfo={this.state.user} goto="/stock-market" gotoname="Get Link" title="Buy Packages" />
                    </Toolbar>
                </React.Fragment>
                
                <CssBaseline />
                <div className={classes.paper}>
                    <Spin spinning={this.state.getting}>
                        <Paper component="form" className={classes.root}>
                            <Table stickyHeader aria-label="sticky table">
                                <TableHead>
                                    
                                    <TableRow>
                                        <TableCell align="center" colSpan="3">
                                            Current Package: <b><i>{this.state.user.packagename}</i></b>
                                        </TableCell>
                                        <TableCell align="center" colSpan="4">Detail</TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="center">Package</TableCell>
                                        <TableCell align="center">Price</TableCell>
                                        <TableCell align="center">Valid Days</TableCell>
                                        <TableCell align="center">Source</TableCell>
                                        <TableCell align="center">Total per day</TableCell>
                                        <TableCell align="center">Price per link</TableCell>
                                        <TableCell align="center">
                                            Action
                                        </TableCell>
                                    </TableRow>

                                </TableHead>
                                <TableBody>
                                    {
                                        this.state.package.map((value, index) => (
                                            value.data.map(function(v2, i2) {
                                                if (i2 === 0) {
                                                    return (
                                                        <TableRow key={v2.maxnum+v2.source} hover selected>
                                                            <TableCell align="center"><b>{value.name}</b></TableCell>
                                                            <TableCell align="center">{value.price} VND</TableCell>
                                                            <TableCell align="center">{value.valid}</TableCell>
                                                            <TableCell align="center">{v2.source}</TableCell>
                                                            <TableCell align="center">{v2.maxnum}</TableCell>
                                                            <TableCell align="center">{v2.prices}</TableCell>
                                                            <TableCell align="center">
                                                                <Popconfirm placement="left" title="Are you sure to buy this pack ?" onConfirm={() => self.actionBuy(value.name)} okText="Yes" cancelText="Cancel">
                                                                    <IconButton>
                                                                        <ShoppingBasketOutlinedIcon/>
                                                                    </IconButton>
                                                                </Popconfirm>
                                                            </TableCell>
                                                        </TableRow>
                                                    )
                                                } else {
                                                    return (
                                                        <TableRow key={v2.maxnum+v2.source}>
                                                            <TableCell align="center" colSpan="3"></TableCell>
                                                            <TableCell align="center">{v2.source}</TableCell>
                                                            <TableCell align="center">{v2.maxnum}</TableCell>
                                                            <TableCell align="center">{v2.prices}</TableCell>
                                                            <TableCell align="center"></TableCell>
                                                        </TableRow>
                                                    )
                                                }
                                            })
                                        ))
                                    }
                                    
                                </TableBody>
                            </Table>
                        </Paper>
                    </Spin>
                </div>
                <Box mt={8}>
                    <Copyright />
                </Box>
            </Container>
            </Spin>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(actions.logout())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Buypackages));