import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import * as api from '../../api';
import PropTypes from 'prop-types';
import { DownloadOutlined, ReloadOutlined } from '@ant-design/icons';
import { Table } from 'antd';

const { Column } = Table;

class HistoryDownload extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Table dataSource={this.props.listlink} pagination={{ pageSize: 8 }} >
                <Column title="Nguồn" dataIndex="source" key="source" />
                <Column title="Link" dataIndex="linkget" key="linkget" />
                <Column title="Code" dataIndex="code" key="code" />
                
                <Column
                    title="Action"
                    key="action"
                    render={(text, record) => (
                        <>
                        {
                            record.isdone ?
                            <a href={record.result} target="_blank" rel="noreferrer" >
                                <DownloadOutlined/> Download
                            </a>
                            :
                            <a>
                                <ReloadOutlined/> Reload
                            </a>
                        }
                        </>
                    )} />
            </Table>
        )
    }
}

HistoryDownload.propTypes = {
    listlink: PropTypes.array,
};

const mapStateToProps = (state) => {
    return {
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(actions.logout())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HistoryDownload)