import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import axios from 'axios';
import * as api from '../../api';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import { Spin, Collapse, Table, Row, Col, Form, Input, Button, notification } from 'antd';
import Toolbar from '@material-ui/core/Toolbar';
import Headered from './headered';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: '60vw',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
    },
    toolbarLink: {
        padding: theme.spacing(1),
        flexShrink: 0,
    },
});

const { Panel } = Collapse;
const { Column } = Table;

class Purchase extends React.Component {
    formRef = React.createRef();

    constructor(props) {
        super(props);
        this.state = {
            getting: false,
            user: {},
            payments: [],
            charging: false
        }
    }

    getuserprofile() {
        this.setState({user:{}});
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_user_detail,{})
        .then(resp => {
            this.setState({user:resp.data})
        })
        .catch(err => {
            console.log(err)
        })
    }

    getpaymentHis() {
        
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.get(api.api_get_payment)
        .then(resp => {
            this.setState({payments:resp.data})
        })
        .catch(err => {
            console.log(err)
        })
    }

    checkpayment = e => {
        this.setState({ charging: true });
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_recharge_momo, {
            transcode: e.transcode
        })
        .then(resp => {
            this.formRef.current.resetFields();
            notification.success({
                message: 'Payment success',
                description: resp.data.msg,
            });
            this.setState({ charging: false });
            this.getuserprofile();
            this.getpaymentHis();
        })
        .catch(err => {
            this.formRef.current.resetFields();
            this.setState({ charging: false });
            notification.error({
                message: 'Payment failed',
                description: err.response.data.msg,
            });
        })
    }

    componentDidMount() {
        this.getuserprofile();
        this.getpaymentHis();
    }
    
    render() {
        const { classes } = this.props;
        
        return (
            <Spin spinning={!this.state.user.username}>
                <Container component="main" maxWidth="lg" style={{overflow: "hidden"}}>
                    <React.Fragment>
                        <Toolbar className={classes.toolbar}>
                            <Headered userinfo={this.state.user} goto="/stock-market" gotoname="Get Link" title="Purchase" />
                        </Toolbar>
                    </React.Fragment>
                    
                    <CssBaseline />
                    <div className={classes.paper}>
                        
                        <Collapse style={{ width: "80vw" }}>
                            <Panel header="Fast recharge (nạp tiền tự động)" key="1">
                                <p>1. Chuyển tiền vào <b>MOMO: 0943623777</b> - LỮ THẾ BẢO</p>
                                <p>2. Kiểm tra <b>Mã giao dịch</b> và điền vào form</p>
                                <Row>
                                    <Col span={8}>
                                        <img alt="momo-demo" src="https://1.bp.blogspot.com/-qjMQo_k2eSc/X7oKl6a0rcI/AAAAAAAAEcY/HUaWivChN60FH89oOpd8CaH8qfwQvBInACLcBGAsYHQ/s1241/momo.jpg" width="100%"></img>
                                    </Col>
                                    <Col span={2}></Col>
                                    <Col span={12}>
                                        <Spin spinning={this.state.charging}>
                                            <Form ref={this.formRef} onFinish={this.checkpayment}>
                                                <Form.Item label="Mã giao dịch" name="transcode"
                                                    rules={[{ required: true, message: 'Hãy nhập mã giao dịch!' }]}>
                                                    <Input />
                                                </Form.Item>
                                                <Form.Item>
                                                    <Button type="primary" htmlType="submit">
                                                        Kiểm tra và nạp
                                                    </Button>
                                                </Form.Item>
                                            </Form>
                                        </Spin>
                                    </Col>
                                </Row>
                            </Panel>
                            <Panel header="Slow recharge (chuyển tiền chậm)" key="2">
                                <p>1. Chuyển 1 trong 2 hình thức dưới đây:</p>
                                <p>- Bank: <b>19956768</b> - Ngân hàng ACB Bảo Lộc - LO DAN TAN TAI</p>
                                <p>- MOMO: <b>0965492202</b> - LO DAN TAN TAI</p>
                                <p>2.Nội dung: <b>getlink {this.state.user.username}</b></p>
                                <p>3.Sau đó liên hệ: <a href="https://www.messenger.com/t/theemotiontome" target="_blank" rel="noreferrer">Facebook</a></p>
                            </Panel>
                        </Collapse>
                    </div>
                    <div className={classes.paper}>
                        <Table dataSource={this.state.payments} pagination={{ pageSize: 8 }} >
                            <Column title="Username" dataIndex="username" key="username" />
                            <Column title="Gate" dataIndex="payways" key="payways" />
                            <Column title="Trans Code" dataIndex="paykeys" key="paykeys" />
                            <Column title="Amout" dataIndex="values" key="values" />
                            <Column title="Time" dataIndex="paytime" key="paytime" />
                        </Table>
                    </div>
                    <Box mt={8}>
                        <Copyright />
                    </Box>
                </Container>
            </Spin>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(actions.logout())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Purchase));