import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/auth';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import InputIcon from '@material-ui/icons/Input';
import { Spin, Popover, Modal } from 'antd';
import Toolbar from '@material-ui/core/Toolbar';
import axios from 'axios';
import HistoryDownload from './history';
import * as api from '../../api';
import Headered from './headered';
import { Button } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';


function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a color="inherit" href="https://facebook.com/luthebao97" target="_blank" rel="noreferrer">
                BaoLT
            </a>
            {' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = theme => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: '60vw',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
    paper: {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
    },
    toolbarLink: {
        padding: theme.spacing(1),
        flexShrink: 0,
    },
});

class Logout extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            getting: false,
            link: "",
            user: {},
            downloaded: false,
            buttons: []
        }
    }

    getuserprofile() {
        this.setState({ user: {} });
        this.setState({ link: "" });
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_user_detail,{})
        .then(resp => {
            this.setState({user:resp.data})
        })
        .catch(err => {
            console.log(err)
        })
    }

    componentDidMount() {
        this.getuserprofile();
    }

    getlinkAction(link) {
        this.setState({getting: true})
        this.setState({downloaded: false})
        this.setState({buttons: []})
        axios.defaults.headers = {
            Authorization: "Token " + this.props.token
        }
        axios.post(api.api_get_link,{
            "linkget" : link
        })
        .then(resp => {
            this.setState({getting: false});
            Modal.success({
                title: 'Success',
                content: 'Get link success, download from the link below.',
            });
            this.setState({downloaded: true});
            this.setState({buttons: resp.data.data});
            this.getuserprofile();
        })
        .catch(err => {
            this.setState({getting: false})
            this.setState({link: ""});
            Modal.error({
                title: 'Get link error',
                content: err.response.data.msg,
            });
        })
    }

    render() {
        const { classes } = this.props;
        const self = this;
        
        return (
            <Spin spinning={!this.state.user.username}>
            <Container component="main" maxWidth="lg" style={{overflow: "hidden"}}>
                <React.Fragment>
                    <Toolbar className={classes.toolbar}>
                        <Headered userinfo={this.state.user} goto="/buy-packages" gotoname="Buy packages" title="Get Link Stock Premium" />
                    </Toolbar>
                </React.Fragment>
                <CssBaseline />
                <div className={classes.paper}>
                    <Spin spinning={this.state.getting}>
                        <Paper component="form" className={classes.root}>
                            <IconButton className={classes.iconButton} aria-label="menu" onClick={() => this.setState({modal1Visible: true})} >
                                <InputIcon />
                            </IconButton>
                            <InputBase
                                className={classes.input}
                                placeholder="Link stock: https://www.freepik.com/premium-psd/something-you-need_123456.htm"
                                onChange={(e) => this.setState({link: e.target.value})}
                                value={this.state.link}
                            />
                            <Divider className={classes.divider} orientation="vertical" />
                            <Popover content="Get link download">
                                <IconButton color="primary" className={classes.iconButton} aria-label="directions" onClick={() => self.getlinkAction(this.state.link)} >
                                    <CloudDownloadIcon />
                                </IconButton>
                            </Popover>
                        </Paper>
                        <div style={{textAlign: 'center'}}>
                            {
                                this.state.buttons.map(
                                    (val, index) => (
                                        <Button style={{margin: "10px"}} shape="round" type="primary" icon={<DownloadOutlined />}
                                            onClick={(e) => window.open(val.down)}
                                        >
                                            {val.text}
                                        </Button>
                                    )
                                )
                            }
                        </div>
                    </Spin>
                </div>
                <HistoryDownload listlink={this.state.user.history} />
                
                <Box mt={8}>
                    <Copyright />
                </Box>
            </Container>
            </Spin>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        logout: () => dispatch(actions.logout())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(useStyles)(Logout));